"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { Container, Row, Col, Card, Button, Alert } from "react-bootstrap";
import { loadStripe } from "@stripe/stripe-js";

interface PackageOption {
  label: string;
  price: number;
  priceId: string;
}
interface PackageData {
  type: string;
  title: string;
  image: string;
  options: PackageOption[];
}

const paquetesData: PackageData[] = [
  {
    type: "agua",
    title: "Estimulación temprana en agua",
    image: "/images/estimlacion_agua_animado.jpeg",
    options: [
      { label: "1 sesión (válido 1 mes)", price: 500, priceId: "price_1RJd0OFV5ZpZiouCasDGf28F" },
      { label: "4 sesiones/mes", price: 1400, priceId: "price_1RMBAKFV5ZpZiouCCnrjam5N" },
      { label: "8 sesiones/mes", price: 2250, priceId: "price_1RMBFKFV5ZpZiouCJ1vHKREU" },
      { label: "12 sesiones/mes", price: 2500, priceId: "price_1RMBIaFV5ZpZiouC8l6QjW2N" },
    ],
  },
  {
    type: "piso",
    title: "Estimulación temprana en piso",
    image: "/images/estimulacion_piso_animado.jpeg",
    options: [
      { label: "1 sesión (válido 1 mes)", price: 500, priceId: "price_1RJd1jFV5ZpZiouC1xXvllVc" },
      { label: "4 sesiones/mes", price: 1400, priceId: "price_1RP6S2FV5ZpZiouC6cVpXQsJ" },
      { label: "8 sesiones/mes", price: 2250, priceId: "price_1RMBIaFV5ZpZiouC8l6QjW2N" },
      { label: "12 sesiones/mes", price: 2500, priceId: "price_1RP6TaFV5ZpZiouCoG5G58S3" },
    ],
  },
  {
    type: "fisioterapia",
    title: "Fisioterapia",
    image: "/images/fisio_animado.jpeg",
    options: [
      { label: "1 sesión", price: 0, priceId: "price_1RJd3WFV5ZpZiouC9PDzHjKU" },
      { label: "10 sesiones", price: 3000, priceId: "price_1RP6W9FV5ZpZiouCBXnZwxLW" },
      { label: "5 sesiones", price: 2000, priceId: "price_1RP6WwFV5ZpZiouCN3m0luq3" },
    ],
  },
];

export default function PackagesSection() {
  const [hasPackages, setHasPackages] = useState(false);

  useEffect(() => {
    // si implementas fetch("/api/user/packages") pon la lógica real aquí
    setHasPackages(false);
  }, []);

  if (!hasPackages) {
    return (
      <Container className="py-4">
        <Alert variant="info" className="text-center">
          No tienes paquetes vigentes.
        </Alert>
        <Row className="g-4">
          {paquetesData.map((pkg) => (
            <Col md={4} key={pkg.type}>
              <Card className="h-100 text-center package-card">
                <div className="card-img-wrap">
                  <Image
                    src={pkg.image}
                    alt={pkg.title}
                    layout="responsive"
                    width={400}
                    height={200}
                    className="card-img-top"
                  />
                </div>
                <Card.Body className="d-flex flex-column">
                  <Card.Title>{pkg.title}</Card.Title>
                  <div className="mt-2 flex-grow-1">
                    {pkg.options.map((opt) => (
                      <div key={opt.priceId} className="d-flex justify-content-between py-1">
                        <span>{opt.label}</span>
                        <strong>${opt.price.toLocaleString()}</strong>
                      </div>
                    ))}
                  </div>
                  <div className="mt-3">
                    {pkg.options.map((opt) => (
                      <Button
                        key={opt.priceId}
                        variant="orange"
                        className="me-2 mb-2"
                        onClick={async () => {
                          const res = await fetch("/api/stripe/checkout", {
                            method: "POST",
                            headers: { "Content-Type": "application/json" },
                            body: JSON.stringify({ priceId: opt.priceId }),
                          });
                          const { sessionId } = await res.json();
                          const stripe = await loadStripe(
                            process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY!
                          );
                          stripe?.redirectToCheckout({ sessionId });
                        }}
                      >
                        ¡Lo quiero!
                      </Button>
                    ))}
                  </div>
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      </Container>
    );
  }

  return <Container>{/* muestra paquetes reales */}</Container>;
}